const swiper = new Swiper('.sale_swiper', {
    slidesPerView: 3,
    spaceBetween: 10,
    breakpoints: {
        320: {
            slidesPerView: 1.15,
        },
        360: {
            slidesPerView: 1.15,
        },
   
        480: {
            slidesPerView: 1.5,
            
        },
        576: {
            slidesPerView: 1.5,
            
        },
        768: {
            slidesPerView: 2,
            grid: {
                rows: 2,
                fill: "row",
              },
        },
        991: {
            slidesPerView: 3,
            grid: {
                rows: 2,
                fill: "row",
              },
        },
        1024: {
            slidesPerView: 3,
            grid: {
                rows: 2,
                fill: "row",
              },
        },
       
    }
});