const swiperMain = document.querySelector('.about_slider');
const swiperNav = document.querySelector('.about_slider_nav');

let sliderMain = new Swiper(swiperMain, {
    spaceBetween: 10,
	loop: true,
	loopedSlides: 5,
    thumbs: {
		swiper: swiperNav,
	},
    breakpoints: {
     
        360: {
            slidesPerView: 1.15,
        },
     
        480: {
            slidesPerView: 1.5,
        },
        768: {
            slidesPerView: 1,
        },
    }
});

let thumbNav = new Swiper(swiperNav, {
    slidesPerView: 5,
	spaceBetween: 10,
	loopedSlides: 5,
	freeMode: true,
	watchSlidesVisibility: true,
	watchSlidesProgress: true,
	loop: true,
});