const swiper = new Swiper('.popular_analizes_swiper', {
    centerMode: false,
    simulateTouch: true,
    touchAngle: 35,
    grabCursor: true,
    spaceBetween: 10,
    breakpoints: {
        360: {
            slidesPerView: 1.15,
        },
        480: {
            slidesPerView: 1.5,
        },
        767: {
            slidesPerView: 2,
        },
        1024: {
            slidesPerView: 3,
        }
    }
});

